html {
  box-sizing: border-box;
  color: red; }

*,
*::before,
*::after {
  box-sizing: inherit; }

button:hover {
  cursor: pointer; }

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: middle; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
